* {
  text-align: justify;
  color: #ffffff;
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.name {
  background-color: #1f1f1f;
}
.container {
  height: 100%;
  margin: 0px;
  padding: 2%;
  background-color: #1f1f1f;
}

ul li {
  list-style: none;
}
/* Main Menu */

/* Contact component */
.contact {
  border: 0px #ffffff solid;
  border-radius: 30px;
  background-color: #454546;
  clear: right;
  padding: 10px 0px 10px 50px;
  width: 50%;
  min-width: 400px;
  margin: 10px 0px 10px 0px;
}

.contact a {
  text-decoration: none;
  color: #ffffff;
}

.professionalSummary,
.skills,
.professionalExperience,
.educationTraining {
  width: 70%;
  min-width: 700px;
  padding: 10px;
}

/* Skills */
.skills {
}

.skills ul li {
  margin-left: 30px;
  list-style: disc;
}

/* Professional Experience */
.professionalExperience {
}
/* Other parts */
.quotation {
  font-weight: bolder;
  font-size: larger;
}
