.mainMenu {
  clear: both;
  margin-bottom: 30px;
  padding: 0;
  margin: 0;
}

.mainMenu ul li {
  background-color: #ffff;
  float: left;
  padding: 10px;
  margin-right: 20px;
}

.mainMenu ul li a {
  text-decoration: none;
  color: #1f1f1f;
}

.sub-menu {
  display: none;
}

.sub-menu:hover {
  display: block;
}
